<template>
    <div class='cinemas'>
        <header>
            <div class='left'>上海</div>
            <div class='center'>影院</div>
            <div class='right'>
                <img src='../../public/search.png' style='width:0.18rem;height:0.18rem;' />
            </div>
        </header>
        <div class='header'></div>
        <div class='navlist'>
            <div>全城</div>
            <div>APP订票</div>
            <div>最近去过</div>
        </div>
        <div class='navlist2'></div>
        <div class='banneritem'>
            <ul>
                <li v-for='data in bannerlist' :key='data.bannerId'>
                    <div class='masterTitle'>{{data.masterTitle}}</div>
                    <div class='name'>{{data.name}}</div>
                </li>
            </ul>
        </div>
        <div class='listwrap'>
            <ul>
                <li v-for='data in listwrap' :key='data.cinemaId'>
                    <div class='listleft'>
                        <div class='listname'>{{data.name}}</div>
                        <div class='listaddress'>{{data.address}}</div>
                    </div>
                    <div class='listright'>
                        <div class='righttop'>￥ {{data.lowPrice | myfilters}} 起</div>
                        <div class='rightbottom'>距离未知</div>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
export default {
  data () {
    return {
      bannerlist: [],
      listwrap: []
    }
  },
  mounted () {
    axios({
      url: 'https://m.maizuo.com/gateway?cityId=310100&k=1323742',
      method: 'get',
      headers: {
        'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"1582288622450971566184","bc":"310100"}',
        'X-Host': 'mall.cfg.cinema.banners'
      }
    }).then(res => {
      // console.log(res.data.data)
      this.bannerlist = res.data.data
    })
    axios({
      url: 'https://m.maizuo.com/gateway?cityId=310100&ticketFlag=1&k=9241158',
      method: 'get',
      headers: {
        'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"1582288622450971566184","bc":"310100"}',
        'X-Host': 'mall.film-ticket.cinema.list'
      }
    }).then(res => {
      // console.log(res.data.data)
      this.listwrap = res.data.data.cinemas
    })
  },
  filters: {
    myfilters: function (data) {
      // console.log(parseInt(data.toString().substring(0,2)))
      return parseInt(data.toString().substring(0, 2))
      // 数字转字符串，再截取2位，再转换成number
    }
  }
}
</script>
<style lang="scss">
    header{
        height: 0.44rem;
        display: flex;
        border-bottom: 0.01rem solid #ededed;
        position: relative;
        position: fixed;
        left: 0;
        right: 0;
        top: 0;
        z-index: 1;
        background: white;
        .left{
            width: 15%;
            font-size: 0.13rem;
            line-height: 0.44rem;
            text-align: center;
        }
        .center{
            width: 70%;
            font-size: 0.17rem;
            line-height: 0.44rem;
            text-align: center;
        }
        .right{
            width: 15%;
            position: relative;
            img{
                display: block;
                position: absolute;
                left: 50%;
                top: 50%;
                margin-left: -0.09rem;
                margin-top: -0.09rem;
            }
        }

    }
    .header{
        height: 0.45rem;
    }
    .navlist{
        font-size: 0.16rem;
        height: 0.49rem;
        border-bottom: 0.01rem solid #ededed;
        display: flex;
        justify-content: space-around;
        line-height: 0.49rem;
        position: relative;
        position: fixed;
        left: 0;
        right: 0;
        top:0.45rem;
        z-index: 1;
        background: white;
    }
    .navlist2{
        height: 0.45rem;
    }
    .banneritem{
        ul{
            li{
                box-sizing: border-box;
                padding: 0.15rem;
                .masterTitle{
                    font-size: 0.15rem;
                    color: #18191a;
                }
                .name{
                    margin-top: 0.05rem;
                    font-size: 0.13rem;
                    color: #797d82;
                }
            }
        }
    }
    .listwrap{
        ul{
            li{
                box-sizing: border-box;
                padding: 0.15rem;
                display: flex;
                justify-content: space-between;
               .listleft{
                   .listname{
                        color: #191a1b;
                        font-size: 0.15rem;
                   }
                   .listaddress{
                        color: #797d82;
                        font-size: 0.12rem;
                        margin-top: 0.05rem;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                        overflow: hidden;
                        max-width: 2.5rem;
                   }
               }
               .listright{
                   .righttop{
                       font-size: 0.1rem;
                       color: #ff5f16;
                   }
                   .rightbottom{
                        font-size: 0.11rem;
                        color: #797d82;
                        margin-top: 0.05rem;
                   }
               }
            }
        }
    }
</style>
